body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-equation {
	font-size: 32px;
	display: inline-block;

	white-space: nowrap;
	margin: 8px auto;

	-webkit-user-select: none;

	   -moz-user-select: none;

	    -ms-user-select: none;

	        user-select: none;
}

.equation-term {
	margin-left: 10px;
	cursor: ew-resize;
}
.equation-term.faded {
	color: #CCC;
}

/***** Media Queries *****/

@media (max-width: 850px) {
	.main-equation {
		font-size: 24px;
	}

	.equation-term {
		margin-left: 8px;
	}
}

@media (max-width: 700px) {
	.mobile-break {
		display: block;
	}
}

.control-bar {
	padding: 0 20px;
}

.control-bar .spacer {
	flex-grow: 1;
}

.control-bar .equation-container {
	display: block;
	overflow: hidden;
}

.control-bar .options {
	white-space: nowrap;
	display: inline-block;
}

/***** Media Queries *****/
@media (max-width: 850px) {
	.control-bar {
		padding: 0 10px;
	}
}

@media (max-width: 700px) {
	.control-bar {
		padding: 0;
	}

	.control-bar .options {
		width: 1px;
		overflow: hidden;
	}
}

.info-card {
	margin: 10px;
	width: calc(100% - 20px);

	display: block;
	position: relative;
}

.info-panel-paper {
	width: 400px;
}

@media (max-width: 700px) {
	.toolbar-padding {
		height: 90px;
	}

	.info-panel-paper {
		width: 100%;
	}
}


#main-view {
	height: 100%;
	width: 100%;
}

#root {
	width: 100%;
	height: 100%;
}

html, body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;
}

